import React from "react";
import "./App.css";
import { Header, Navigation, Content } from "react-mdl";
import Projects from "./Components/projects";
import LandingPage from "./Components/landingpage";
import AboutMe from "./Components/aboutme";
import Contact from "./Components/contact";
import AOS from "aos";
import "aos/dist/aos.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      bgColor1: "",
      bgColor2: "",
      bgColor3: "",
      size: "50px",
      h3Color: "white"
    };
  }
  componentDidMount() {
    window.addEventListener("scroll", this.highlight_menu, false);
    AOS.init({
      duration: 2000
    });
  }

  componentWillUnmount = () => {
    window.removeEventListener("scroll", this.highlight_menu);
  };

  highlight_menu = () => {
    let offsetY = window.pageYOffset;
    /* This if statement is for H3 tag of my name for User Scroll*/
    if (offsetY > 10) {
      this.setState({
        size: "35px"
      });
    } else {
      this.setState({
        size: "50px",
        h3Color: "white"
      });
    }

    /* This if statement is for the menu header items for User Scroll*/
    if (offsetY >= 0 && offsetY <= 849) {
      this.setState({
        bgColor1: "",
        bgColor2: "",
        bgColor3: ""
      });
    } else if (offsetY >= 850 && offsetY <= 1349) {
      this.setState({
        bgColor1: "#06beb6",
        bgColor2: "",
        bgColor3: ""
      });
    } else if (offsetY >= 1350 && offsetY <= 2599) {
      this.setState({
        bgColor1: "",
        bgColor2: "#06beb6",
        bgColor3: ""
      });
    } else if (offsetY >= 2600) {
      this.setState({
        bgColor1: "",
        bgColor2: "",
        bgColor3: "#06beb6"
      });
    }
  };

  /* These functions are for the onClick events for the menu header items */
  boxClick0 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "grey";
    this.setState({
      bgColor1: "",
      bgColor2: "",
      bgColor3: "",
      h3Color: "grey"
    });
  };

  boxClick1 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "#06beb6";

    this.setState({
      bgColor1: "#06beb6",
      bgColor2: "",
      bgColor3: "",
      h3Color: "#06beb6"
    });
  };

  boxClick2 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "white";
    this.setState({
      bgColor1: "",
      bgColor2: "#06beb6",
      bgColor3: "",
      h3Color: "white"
    });
  };

  boxClick3 = e => {
    let scr = document.getElementById("name");
    scr.style.color = "aqua";
    this.setState({
      bgColor1: "",
      bgColor2: "",
      bgColor3: "#06beb6",
      h3Color: "aqua"
    });
  };

  render() {
    return (
      <div className="content">
        <Header className="header-color" scroll data-aos="fade">
          <Navigation data-aos="slide-right">
            <a
              id="name"
              href="#/LP"
              style={{ textAlign: "left" }}
              onClick={this.boxClick0}
            >
              <h3
                style={{
                  fontSize: this.state.size,
                  transition: "linear 0.6s",
                  color: this.state.h3Color
                }}
              >
                Anthony Hsia
              </h3>
            </a>
            <a
              className="about_me"
              href="#AboutMe"
              style={{ backgroundColor: this.state.bgColor1 }}
              onClick={this.boxClick1}
            >
              About Me
            </a>
            <a
              className="projects"
              href="#Projects"
              style={{ backgroundColor: this.state.bgColor2 }}
              onClick={this.boxClick2}
            >
              Projects
            </a>
            <a
              className="contact"
              href="#Contact"
              style={{ backgroundColor: this.state.bgColor3 }}
              onClick={this.boxClick3}
            >
              Contact
            </a>
          </Navigation>
        </Header>
        <Content id="/LP">
          <section>
            <LandingPage />
          </section>
          <section id="AboutMe">
            <AboutMe />
          </section>

          <section id="Projects">
            <Projects />
          </section>
          <section id="Contact">
            <Contact />
          </section>
        </Content>
      </div>
    );
  }
}

export default App;
